import { useContext } from "react";
import { MSPRContext } from "../MSPRPolicy";
import { TextField, Divider, Button } from "@mui/material";

export default function MSPRPolicyPremium() {
  const {
    state,
    handleInputChange,
    computation,
    isAddOrEditMode,
  } = useContext(MSPRContext);


  return (
    <div style={{ display: "flex" }}>
      <div
        style={{
          display: "flex",
          flex: 1,
          flexDirection: "column",
        }}
      >
        <fieldset
          style={{
            display: "flex",
            gap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            flex: 1,
            justifyContent: "center",
          }}
        >
          <legend style={{ height: "22px" }}></legend>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              rowGap: "10px",
              flex:1
            }}
          >
            <TextField
              fullWidth
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Section I Insurance"
              name="sec1"
              value={state.sec1}
              onChange={handleInputChange}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Section IB Insurance"
              name="sec2"
              value={state.sec2}
              onChange={handleInputChange}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Section II Insurance"
              name="sec3"
              value={state.sec3}
              onChange={handleInputChange}
            />
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              rowGap: "10px",
              flex:1
            }}
          >
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Premium"
              name="prem1"
              value={state.prem1}
              onChange={handleInputChange}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Premium"
              name="prem2"
              value={state.prem2}
              onChange={handleInputChange}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Premium"
              name="prem3"
              value={state.prem3}
              onChange={handleInputChange}
            />
          </div>
        </fieldset>
      </div>
      <fieldset
        style={{
          display: "flex",
          flexDirection: "column",
          rowGap: "10px",
          padding: "15px",
          border: "1px solid #cbd5e1",
          borderRadius: "5px",
        }}
      >
        <legend>Premiums</legend>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            padding: "5px",
          }}
        >
          <Button
            disabled={isAddOrEditMode}
            size="small"
            variant="contained"
            color="primary"
            onClick={() => {
              computation();
            }}
          >
            Compute
          </Button>
        </div>

        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Net Premium"
          name="netPremium"
          value={state.netPremium}
          onChange={handleInputChange}
        />
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Vat"
          name="vat"
          value={state.vat}
          onChange={handleInputChange}
        />
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Doc Stamp"
          name="docStamp"
          value={state.docStamp}
          onChange={handleInputChange}
        />
        <div style={{ display: "flex", gap: "5px", position: "relative" }}>
          <TextField
            sx={{ width: "80px" }}
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            name="localGovTaxPercent"
            value={state.localGovTaxPercent}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Local Gov Tax"
            name="localGovTax"
            value={state.localGovTax}
            onChange={handleInputChange}
          />
        </div>
        <Divider color="secondary" />
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Total Due"
          name="totalDue"
          value={state.totalDue}
          onChange={handleInputChange}
        />
      </fieldset>
    </div>
  );
}
